<template>
    <h1>{{ activeTag }} Projects</h1>
    <div class="postList">
        <div class="tagArea">
            <div class="active">
                <!-- <label>Current Filter: {{ activeTag }}</label> -->
            </div>
            <!-- <div class="active">

            </div> -->
            <button v-bind:class="getClass('')" @click="activeTag=''">All</button>
            <button v-bind:class="getClass('Game')" @click="activeTag='Game'">Games</button>
            <!-- <button @click="activeTag='JavaScript'">JavaScript</button> -->
            <button v-bind:class="getClass('3D')" @click="setActiveTag('3D')">3D</button>
            <button v-bind:class="getClass('Programming')" @click="activeTag='Programming'">Programming</button>
            <button v-bind:class="getClass('Game Design')" @click="activeTag='Game Design'">Game Design</button>
            <!-- <button @click="activeTag='Unity'">Unity</button> -->
            <!--button @click="activeTag='Blender'">Blender</button-->
           
            <!-- <button @click="activeTag='C#'">C#</button> -->
            <!-- <button @click="activeTag='Videogame'">Videogame</button> -->
            <!-- <button @click="activeTag='Rigging'">Rigging</button> -->
            <!-- <button @click="activeTag='Texturing'">Texturing</button> -->
            <!-- <button @click="activeTag='Animation'">Animation</button> -->
        </div>
        <div class="posts">
            <div v-for="post in posts" :key="post.id">
                <!--h3>{{ post.title }}</h3-->
                <div v-if="activeTag==''">
                    <SinglePost :post="post" />
                </div>
                <div v-else-if="searchTag(activeTag, post.id)">
                    <SinglePost :post="post"/>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import SinglePost from './SinglePost.vue'
import { ref } from 'vue'
export default {
    props: ['posts'],
    components: {SinglePost},
    setup(props)
    {
//        onMounted(() =>console.log('component mounted'))
        const activeTag = ref('')
        const searchTag = (tag, postId)=>
        {
            //for(var i=0;i<props.posts.length;i++)
            //{
            var realId = props.posts.length - postId;
                for(var j=0;j<props.posts[realId].tags.length;j++)
                {
                    if(props.posts[realId].tags[j].match(tag))
                    {
                        console.log(props.posts[realId]);
                        return true;
                    }   
                }
            //}
        }
        const setActiveTag = (tag) =>
        {
        activeTag.value=tag;
        }
        const getClass = (name) =>
        {
            if(activeTag.value==name)
                return "activeTag"
            else   
                return "notActiveTag"
        }
        return {activeTag,searchTag, setActiveTag, getClass}
    },

}
</script>

